import React, { Component } from 'react';
import './Project.css';
import test from "../image/page.jpg"
import image1 from "../image/1.jpg"
import { Link } from 'react-router-dom'

class About extends Component {
  render() {
    return (
      <div >
        {/* //////////////overlay 1 //////////////// */}
       
        <div  >
        <Link to='/'>
          <img src={test}  alt="" />
          </Link >

          <Link to='/'>
             <img src={image1}  alt="" />
             </Link >


        </div>
      </div>

    );
  }
}

export default About;



